function Conclusion(
    answerList,
    selfList,
    otherList,
    setSelfList,
    setOtherList
){
    let tmp_selfList=[];
    let tmp_otherList=[];
    let tmp="";
    let cnt_FT=0;

    console.log("answerList:",answerList);

    answerList.forEach((item,index)=>
    {
        //console.log("selfList, otherList", selfList,otherList);
        let num = index+1;
        // console.log("answerList's item : ",item);
        // console.log("item's type, num : ",typeof(item),num);
        switch(num){
            case 2:
                if(item===1)
                    tmp = "     "
                else if(item===2)
                    tmp = "     "
                tmp_selfList.push(tmp);
                //forEach map set  
                //set        case      
                break;
            case 3:
                if(item===1)
                    tmp = ""
                else if(item===2)
                    tmp = ""
                else if(item===3)
                    tmp = ""
                tmp_otherList.push(tmp);
                break;
            case 4:
                if(item===1)
                    cnt_FT++;
                else if(item===2)
                    cnt_FT--;
                break;
            case 5:
                if(item===1)
                    tmp = ""
                else if(item===2)
                    tmp = ""
                else if(item===3)
                    tmp = ""
                tmp_selfList.push(tmp);
                break;
            case 6:
                if(item===1)
                    tmp = "      "
                else if(item===2)
                    tmp = "     "
                tmp_selfList.push(tmp);
                break;
            case 8:
                if(item===1)
                    tmp = " "
                else if(item===2)
                    tmp = "       "
                tmp_selfList.push(tmp);
                break;
            case 9:
                if(item===1){
                    tmp = "  "
                    cnt_FT++;
                }
                else if(item===2){
                    tmp = "  "
                    cnt_FT--;
                }
                tmp_selfList.push(tmp);
                break;
            case 10:
                if(item===1){
                    tmp = "     "
                    cnt_FT++;
                }
                else if(item===2){
                    tmp = "   "
                    cnt_FT--;
                }
                tmp_selfList.push(tmp);
                break;
            default:
                break;
        }
    })

    if(cnt_FT>0){
        tmp=""
        tmp_otherList.push(tmp);
    }else{
        tmp=""
        tmp_otherList.push(tmp);
    }

    setSelfList(tmp_selfList);
    setOtherList(tmp_otherList);
    console.log("selfList,otherList complete :",
    selfList,otherList);
}

